import React, { Component } from "react";
import {
	Route,
	NavLink,
	BrowserRouter
} from "react-router-dom";
import Home from "./pages/home";
import Seed from "./pages/seed";
import About from "./pages/about";
import Information from "./pages/information";
import Team from "./pages/team";
import Partners from "./pages/partners";
import Contacts from "./pages/contacts";
import ScrollArea from "react-scrollbar";
import Footer from "./footer";
import Sunflower from "./pages/seed/sunflower";
import Soy from "./pages/seed/soy";
import Raps from "./pages/seed/raps";
import BarleyWinter from "./pages/seed/barley_winter";
import WheatSpring from "./pages/seed/wheat_spring";
import WheatWinter from "./pages/seed/wheat_winter";
import BarleySpring from "./pages/seed/barley_spring";
import Clients from "./pages/partners/clients";
import Outspread from "./pages/partners/outspread";
import Distributors from "./pages/partners/distributors";
import MobileMenu from "./scripts/mobileMenu";
import { slide as Menu } from 'react-burger-menu'

class Main extends Component {
	render() {
		return (
			<BrowserRouter>
				<ScrollArea
		            speed={1}
		            className="area"
		            contentClassName="scroll-area"
		            vertical={true}
		            horizontal={false}
		            smoothScrolling={false}
		            style={{ height: '100%'}}
	            >
					<div className="main">
						<div className="top-bar">
							<div id="outer-container">
			  					<Menu right width={ '200px' } >
									<ul className="nav-mobile">
										<li className="menu-item"><NavLink to="/" strict exact></NavLink></li>
										<li className="menu-item"><NavLink to="/seed" strict exact></NavLink></li>
										<li className="menu-item"><NavLink to="/about"> </NavLink></li>
										<li className="menu-item"><NavLink to="/team"></NavLink></li>
										<li className="menu-item"><NavLink to="/partners"></NavLink></li>
										<li className="menu-item"><NavLink to="/contacts"></NavLink></li>
									</ul>
								</Menu>
							</div>
							<div className="content row">
								<NavLink to="/" strict exact>
									<span className="logo" />
								</NavLink>
								<ul className="menu">
									<li className="menu-item"><NavLink to="/" strict exact></NavLink></li>
									<li className="menu-item submenu-link">
										<NavLink to="/seed"></NavLink>
										<ul className="submenu">
											<li className="submenu-item"><NavLink replace to="/seed/wheat/winter"> </NavLink></li>
											<li className="submenu-item"><NavLink replace to="/seed/wheat/spring"> </NavLink></li>
											<li className="submenu-item"><NavLink replace to="/seed/soy"></NavLink></li>
											<li className="submenu-item"><NavLink replace to="/seed/sunflower"></NavLink></li>
											<li className="submenu-item"><NavLink replace to="/seed/barley/spring"> </NavLink></li>
											<li className="submenu-item"><NavLink replace to="/seed/barley/winter"> </NavLink></li>
											<li className="submenu-item"><NavLink replace to="/seed/raps"></NavLink></li>
										</ul>
									</li>
									<li className="menu-item"><NavLink to="/about"> </NavLink></li>
									<li className="menu-item hide"><NavLink to="/information"> </NavLink></li>
									<li className="menu-item"><NavLink to="/team"></NavLink></li>
									<li className="menu-item">
										<NavLink to="/partners"></NavLink>
										<ul className="submenu">
											<li className="submenu-item"><NavLink replace to="/partners/distributors"> </NavLink></li>
											<li className="submenu-item"><NavLink replace to="/partners/outspread">  </NavLink></li>
											<li className="submenu-item"><NavLink replace to="/partners/clients"></NavLink></li>
										</ul>
									</li>
									<li className="menu-item"><NavLink to="/contacts"></NavLink></li>
								</ul>
							</div>
						</div>
						
						<Route exact path="/" component={Home}/>
						<Route path="/seed" component={Seed}/>
						<Route path="/about" component={About}/>
						<Route path="/information" component={Information}/>
						<Route path="/team" component={Team}/>
						<Route path="/partners" component={Partners}/>
						<Route path="/contacts" component={Contacts}/>

						<Route path="/seed/wheat/winter" component={WheatWinter}/>
						<Route path="/seed/wheat/spring" component={WheatSpring}/>
						<Route path="/seed/soy" component={Soy}/>
						<Route path="/seed/sunflower" component={Sunflower}/>
						<Route path="/seed/barley/spring" component={BarleySpring}/>
						<Route path="/seed/barley/winter" component={BarleyWinter}/>
						<Route path="/seed/raps" component={Raps}/>

						<Route exact path="/partners/distributors" component={Distributors}/>
						<Route path="/partners/outspread" component={Outspread}/>
						<Route path="/partners/clients" component={Clients}/>
					</div>
					<Footer />
				</ScrollArea>
			</BrowserRouter>
		);
	}
}

export default Main;


