class Product extends Component {
	render() {

		let product_dialog_styles = {
			dialogStyles: {
				padding: '2rem',
				width: '1100px',
				marginTop: '-380px',
				marginLeft: '-550px'
			},
			closeButtonStyle: {
				cursor: 'pointer',
				position: 'absolute',
				fontSize: '3rem',
				lineHeight: '1.7rem',
				fontWeight: '100',
				right: '10px',
				top: '10px',
				color: '#004E27',
				overflow: 'hidden'
			}
		};

		return (
			<div className="product-item-container">
				<button className="product-item-mask" onClick={() => this.productDetails.show()}>
					<div className="product-item">
						<img src={require(`../../../images/seeds/${this.props.seed.img}.jpg`)} alt="" />
					</div>
					<div className="seed-name">{this.props.seed.name}</div>
					<div className="seed-type">
						<div className="seed-type-caption">
							{this.props.seed.seed_type}
						</div>
					</div>
					<div className="seed-logo" />
				</button>
				<SkyLight dialogStyles={product_dialog_styles.dialogStyles} closeButtonStyle={product_dialog_styles.closeButtonStyle} hideOnOverlayClicked ref={ref => this.productDetails = ref}>
					<div className="product-popup">
						<div className="product-title">
							<h3>{this.props.seed.name}</h3>
						</div>
						<div className="product-item">
							<div className="product-image">
								<img src={require(`../../../images/seeds/${this.props.seed.img}.jpg`)} alt="" />
							</div>
							<Details seed={this.props.seed} seeds={this.props.seeds} />
						</div>
					</div>
				</SkyLight>
			</div>
		);
	}
}

class Products extends Component {
	render() {
		const items = [];
		this.props.seeds.forEach((product) => {
			items.push(
				<Product
					seed={product}
					seeds={this.props}
					name={this.props.name}
				/>
			)});

		return (
			<div className="products">

				{items}

			</div>
		);
	}
}

export default Products;


