import React, { Component } from 'react';
import ReactDOM from 'react-dom';


export default class Map extends Component {

	componentDidMount() {
		this.loadMap();
	}

	loadMap() {
		if (this.props && this.props.google) {
			const {google} = this.props;
			const maps = google.maps;
			const mapRef = this.refs.map;
			const node = ReactDOM.findDOMNode(mapRef);

			const location = {
				ukraine: [ 50.464375,30.5218501, '<strong>Ukraine</strong><br>8 Illinska Street <br>Kiev, Ukraine'  ],
				master: [ -31.92754, 115.85142, '<strong>Australia</strong><br>Lvl 2, 8 Howlett Street  <br> North Perth, WA' ],
				manufacture: [ -33.91166, 151.1927846, '<strong>Australia</strong><br>47 Bourke Rd  <br> Alexandria, NSW' ],
			};

			const mapConfig = Object.assign({}, {
				center: new maps.LatLng(location.ukraine[0],location.ukraine[1]),
				zoom: 14,
				scrollwheel: false,
				mapTypeControl: false,
				disableDefaultUI: true,
				zoomControl: true,
				mapTypeId: 'roadmap',
				scaleControl: false,
				streetViewControl: false,
				disableDoubleClickZoom: true,
				styles: [
					{
						stylers: [{ hue: "#00971f" }]
					}, {
						featureType: "road",
						elementType: "geometry",
						stylers: [
							{ lightness: 90 },
							{ visibility: "simplified" }
						]
					}
				]
			});

			const marker = new maps.Marker({
				maps: maps,
				position: new maps.LatLng(location.ukraine[0],location.ukraine[1])
				// ,
				// icon: '/images/contact/map-marker.svg'
			});

			// infowindow.open(map,marker);
			// setTimeout(moveMap, 5);

			this.map = new maps.Map(node, mapConfig, marker);

			// this.state.locations.forEach( location => {
			// 	const marker = new google.maps.Marker({
			// 		position: {lat: location.location.lat, lng: location.location.lng},
			// 		map: this.map,
			// 		title: location.name
			// 	});
			// })
		}
	}



	render() {
		const style = {
			width: '100%',
			height: '23rem'
		};

		return (
			<div ref="map" style={style} />
		)
	}
}


